import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import BannerLanding from '../components/BannerLanding'

import pic08 from '../assets/images/pic08.jpg'
import pic09 from '../assets/images/pic09.jpg'
import pic10 from '../assets/images/pic10.jpg'

const Attend = (props) => (
    <div>
        <Helmet>
            <title>Attend a Service - Faith Family Church</title>
            <meta name="description" content="what is a service like, what to expect" />
        </Helmet>

        <div id="main">
            <section id="banner" className="style2">
                <div className="inner">
                    <header className="major">
                        <h1>Attend a Service?</h1>
                    </header>
                    <div className="content">
                        <p>What to Expect</p>
                    </div>
                </div>
            </section>
            <section id="one">
                <div className="inner">
                    <header className="major">
                        <h2>Welcome Home!</h2>
                    </header>
                    <p>Welcome to Faith Family Church.  We hope when you walk through the doors
you will immediately recognize that you are among friends--a diverse,
wonderful group of people who love Jesus and who are committed to
helping you discover yourself in the light of the Word of God.  We have
been praying for you, and no matter your background or life experience,
you'll find a place where you're warmly welcomed and feel right at home!</p>
                </div>
            </section>
            <section id="two" className="spotlights">
                <section>
                    <Link to="/statement" className="image">
                        <img src={pic08} alt="" />
                    </Link>
                    <div className="content">
                        <div className="inner">
                            <header className="major">
                                <h3>Our Mission</h3>
                            </header>
                            <p>...is to carry on the works of Jesus...</p>
							<p><i>The Spirit of the Lord is upon me, because He hath anointed me to preach the gospel to the poor; He hath sent to heal the brokenhearted, to preach deliverance to the captives, and recovering of sight to the blind, to set at liberty them that are bruised, to preach the acceptable year of the Lord.</i> Luke 4:18-19</p>
                            <ul className="actions">
                                <li><Link to="/statement" className="button">What Do We Believe?</Link></li>
                            </ul>
                        </div>
                    </div>
                </section>
                <section>
                    <Link to="/expect" className="image">
                        <img src={pic09} alt="" />
                    </Link>
                    <div className="content">
                        <div className="inner">
                            <header className="major">
                                <h3>What should you expect?</h3>
                            </header>
                            <p>Probably not “church as usual,” you and your family can expect a genuine, refreshing and fun experience with the Lord. Here are a few things you might want to know.</p>
                            <ul className="actions">
                                <li><Link to="/expect" className="button">What to Expect</Link></li>
                            </ul>
                        </div>
                    </div>
                </section>
                <section>
                    <Link to="/contact" className="image">
                        <img src={pic10} alt="" />
                    </Link>
                    <div className="content">
                        <div className="inner">
                            <header className="major">
                                <h3>Where Are We?</h3>
                            </header>
                            <p>Call us, email us, find us<br/>
							Service times are Sunday @ 10:25am and Wednesday @ 7:00pm</p>
                            <ul className="actions">
                                <li><Link to="/contact" className="button">Contact</Link></li>
                            </ul>
                        </div>
                    </div>
                </section>
                <section>
                    <Link to="/expect" className="image">
                        <img src={pic09} alt="" />
                    </Link>
                    <div className="content">
                        <div className="inner">
                            <header className="major">
                                <h3>Take the 12 Week Challenge</h3>
                            </header>
                            <p>It’s hard to judge a church by attending only one service, so we’ve got a challenge for you. Come each week for 12 weeks, and if you don’t see marked improvements in your daily and spiritual life, perhaps we’re not the church for you; but if you do see a change, then keep on coming because that’s just the beginning of all God has for you! We are confident in God’s ability to transform your life.</p>
                        </div>
                    </div>
                </section>
            </section>
        </div>

    </div>
)

export default Attend


